.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 55px 1fr;
  gap: 1px 1px;
  grid-template-areas: "Nav Nav Nav Nav Nav" "Articles Articles Articles Articles Articles";
  background-color: gray;
}

.create-article-grid-container {
  display: grid;
  grid-template-columns: 1fr 0.5fr 0.5fr 0.5fr 1fr;
  grid-template-rows: 30px 30px 30px 30px;
  gap: 1px 1px;
  grid-template-areas: "t_title . t_url t_url ." ". . . . ." "t_desc . t_submit . ." "t_desc t_desc t_desc . .";
  border-style: solid;
  margin: 2px;
  padding: 3px;
}

.t_title { grid-area: t_title; }

.t_desc { grid-area: t_desc; width: 83.75%}

.t_url { grid-area: t_url; }

.t_submit { grid-area: t_submit; }

.Filter {
  border-style: solid;
  margin: 2px;
  margin-top: 3px;
  padding: 3px;
  padding: 2px;
  width: 99.7vw;
  height: 55px;
}

.Articles {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  gap: 1px 1px;
  grid-template-areas: ".";
  grid-area: Articles;
  margin-top: 2px;
}

.nav {
  /* grid-row: 1/2;
  grid-column: 1/6; */
  background-color: lightslategray;
  padding-left: 4px;
  grid-area: Nav;
  border-bottom: solid;
}

.post-grid-container {
  display: grid;
  grid-template-columns: 4fr 1fr 1fr 1fr;
  /* grid-template-rows: 5% 5% 5% 5% 5% 5% 5% 5% 5% 1fr; */
  grid-template-rows: repeat(auto-fit, minmax(40px, 1fr));
  gap: 1px 1px;
  background-color: lightgrey;
  border-style: solid;
  margin: 2px;
  padding: 3px;
  width: 99.7vw;
}

.Title {
  grid-area: 1 / 1 / 2 / 2;
}

.Description {
  grid-area: 2 / 1 / 7 / 2;
  overflow-wrap: anywhere;
}

.URL {
  grid-area: 9 / 1 / 10 / 2;
}

.Timestamp {
  grid-area: 9 / 4 / 10 / 5;
}

.Sourcefinder {
  grid-area: 1 / 2 / 3 / 5;
}

.Comments {
  grid-area: 10 / 1 / 11 / 5;
  border-style: solid;
  margin: 2px;
  padding: 3px;
}
